<template>
  <div class="container">
    <div
      v-if="step === 'create-pass'"
      class="container-back"
      @click="step = 'main'"
    >
      <v-icon name="chevron-left"></v-icon>
      <div>назад</div>
    </div>
    <div class="container-title">
      <span class="subtitle">Регистрация на конкурс "Студент года 2021"</span>
      <h2 class="page-title">Этап 1. Основные сведения</h2>
      <span class="prompt">* - поля, обязательные для заполнения</span>
    </div>
    <x-form
      v-if="step === 'main'"
      v-model="form"
      :values="form"
      name="application"
      class="user-application-container"
      @submit="toNextStep('next')"
    >
      <div class="step-1">
        <application-form :items="items"> </application-form>
        <a-row :gutter="[0, 30]" class="clear-class">
          <div class="confirm-checkbox">
            <x-input
              type="checkbox"
              name="confirmCheckbox1"
              validation="acceptAll"
              :validation-rules="{
                acceptAll: ({ value }) => {
                  return value.length === 1
                },
              }"
              :validation-messages="{
                acceptAll: 'Нужно принять соглашение',
              }"
              :options="checkboxes"
              error-behavior="submit"
            >
            </x-input>
            <label class="label" for="formulate--application-26_clause">
              Ознакомлен и согласен с
              <router-link to="#"
                >Положением о проведении конкурса по присуждению премии в
                области развития профессионального образования Самарской области
                «Студент года 2021»</router-link
              >
            </label>
          </div>
          <div class="confirm-checkbox">
            <x-input
              type="checkbox"
              name="confirmCheckbox2"
              :options="checkboxes"
              error-behavior="submit"
              validation="acceptAll"
              :validation-rules="{
                acceptAll: ({ value }) => {
                  return value.length === 1
                },
              }"
              :validation-messages="{
                acceptAll: 'Нужно принять соглашение',
              }"
            >
            </x-input>
            <label class="label" for="formulate--application-28_clause">
              Даю согласие на обработку моих персональных данных.
              <router-link to="#"
                >С Политикой обработки персональных данных
                ознакомлен</router-link
              >
            </label>
          </div>
          <a-col
            :md="{ span: 12, offset: 6 }"
            :xl="{ span: 8, offset: 8 }"
            :lg="{ span: 16, offset: 4 }"
            :xs="24"
            class="inline-buttons"
          >
            <a-button
              :disabled="loading"
              type="info"
              block
              html-type="submit"
              class="login-form-button"
              @onSubmit="toNextStep('back')"
            >
              Сохранить и выйти
            </a-button>
            <a-button
              :disabled="loading"
              type="primary"
              block
              html-type="submit"
              class="login-form-button"
              @onSubmit="toNextStep('next')"
            >
              Сохранить и продолжить
            </a-button>
          </a-col>
        </a-row>
      </div>
    </x-form>
    <x-form v-else @submit="onSubmit" v-model="createPassForm" name="pass-form">
      <div class="step-2">
        <a-row :gutter="[24]">
          <a-col
            :xs="{ span: 24, offset: 0 }"
            :md="{ span: 12, offset: 6 }"
            :lg="{ span: 12, offset: 6 }"
            :xl="{ span: 8, offset: 8 }"
            class="center"
          >
            <x-input
              label="Придумайте пароль"
              type="password"
              name="password"
              validation="min:8,length"
              error-behavior="submit"
            >
            </x-input>
            <x-input
              label="Повторите пароль"
              type="password"
              name="password_confirm"
              validation="min:8,length|confirm"
              validation-name="Password confirmation"
              error-behavior="submit"
            >
            </x-input>
            <a-button
              :disabled="loading"
              type="primary"
              block
              html-type="submit"
              class="login-form-button"
            >
              Завершить регистрацию
            </a-button>
          </a-col>
        </a-row>
      </div>
    </x-form>
  </div>
</template>

<script>
import cloneDeep from 'lodash.clonedeep'
import ApplicationForm from '@/components/Student/ApplicationForm.vue'
import userMixin from '@/mixins/user.mixin'

export default {
  components: { ApplicationForm },
  mixins: [ userMixin ],
  data() {
    return {
      form: {
          apartment: '',
          citizenship: null,
          citizenshipCheckbox: '',
          course_education: null,
          date_birth: '',
          district: '',
          email: '',
          first_name: '',
          form_education: '',
          form_education_another: '',
          gender: '',
          house: '',
          last_name: '',
          locality: '',
          nomination: [],
          patronymic: '',
          phone_number: '',
          place_education: '',
          postal_code: '',
          region: 'Самарская область',
          street: '',
          url_vk: '',
          confirmCheckbox1: [],
          confirmCheckbox2: [],
      },
      // form: {
      //   apartment: '305',
      //   citizenship: null,
      //   citizenshipCheckbox: 'РФ',
      //   course_education: 3,
      //   date_birth: '2000-11-30',
      //   district: 'Самара',
      //   email: 'admin@admin.ru',
      //   first_name: 'Денис',
      //   form_education: 'Очная',
      //   form_education_another: '',
      //   gender: 'male',
      //   house: '140',
      //   last_name: 'Рыбачек',
      //   locality: 'Самара',
      //   nomination: [],
      //   patronymic: 'Андреевич',
      //   phone_number: '+79528224017',
      //   place_education: 'universities',
      //   postal_code: '344009',
      //   region: 'Самарская область',
      //   role: 'Супер админ',
      //   street: 'Тургенева',
      //   url_vk: 'https://vk.com/audios295891649',
      //   name_place_education: 'СамГТУ',
      //   confirmCheckbox1: ['clause'],
      //   confirmCheckbox2: ['clause'],
      // },
      checkboxes: {
        clause: ``,
      },
      createPassForm: {
        password: null,
        password_confirm: null,
      },
      nomination: [],
      step: 'main',
      loading: false,
      errorsObj: {},
    }
  },
  watch: {
    form() {
      // console.log(this.form)
    },
  },
  async mounted() {
    await this.getNominations()
  },
  methods: {
    cloneData(data) {
      return cloneDeep(data)
    },
    toNextStep(type) {
      this.afterType = type
      this.step = 'create-pass'
    },
    toPrevStep() {
      this.step = 'main'
    },
    async onSubmit() {
      this.loading = true
      try {
        const data = this.cloneData({
          ...this.form,
          form_education: this.form.form_education_another ? this.form.form_education_another : this.form.form_education,
          citizenship: this.form.citizenshipCheckbox === 'РФ' ? 'РФ' : this.form.citizenship,
          password: this.createPassForm.password,
        })
        await this.$store.dispatch('app/saveEntity', {
          entityName: 'registration',
          data,
          method: 'post',
        })
        
        await this.$store.dispatch('user/login', {
          form: {
            email: data.email,
            password: data.password
          },
          redirect: `/${this.afterType === 'next' ? 'achievements' : ''}`
        })

        this.$notification.success({
          message: `Подтвердите почту для участия в конкурсе`,
          description: `Письмо с подтверждением отправлено на почту`,
        })
      } catch (e) {
        console.log(e)
        if (e?.response?.status === 400) {
          this.step = 'main'
          this.$formulate.handle(e.response.data, 'application')
          // this.$errors.show(e.response.data)
        } else {
          this.$errors.showOnce('Ошибка входа')
        }
      } finally {
        this.loading = false
      }
    },
    trimWhitespacedAndToInt(data) {
      for (let item in data) {
        if (typeof data[item] == 'string') {
          data[item] = data[item].trim()
        }
        if (
          data[item] == Number(data[item]) &&
          !data[item].includes('+') &&
          item !== 'nomination'
        ) {
          data[item] = Number(data[item])
        }
      }
    },
    async getNominations() {
      try {
        await this.$store.dispatch('app/getEntities', {
          entityName: 'nomination',
        })
      } catch (e) {
        console.log(e)
      }
    },
  },
}
</script>

